<template>
    <div class="kechenglist">
        <div v-for="item in kechenglist" :key="item.name">
            <div class="wenzhang" @click="goTohtml(item)">
                <div class="kuai">
                    <div class="textkuang">
                        <div class="title">{{item.title}}</div>
                        <div class="minlan">
                            <div class="laiyuan">{{item.count}}次学习</div>
                            <div class="yigoumai">¥{{item.jiage}}</div>
                            <div class="yigoumai" v-if="item.yigoumai">已购买</div>
                            <div class="yigoumai" v-if="item.yigoumai==0">未购买</div>
                        </div>
                    </div>
                    <img class="image" :src="item.image" mode="scaleToFill" lazy-load="false" />
                </div>
            </div>
        </div>
        <div class="textend" v-if="!isend">加载中......</div>
        <div class="textend" v-if="isend">恋爱脱单上恋爱联盟</div>
    </div>
</template>

<script>
    import api from "@/api/index";
    import wx from 'weixin-js-sdk';
    export default {
        async mounted() {
            this.unionid=localStorage.getItem('unionid') || '';
            if (this.unionid=='') {
                if (!this.$route.query.code) {
                    var href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx2cc1bc5a412d44d2&redirect_uri=http%3A//www.lianaizhuli.com/kechenglist&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect';
                    window.location.href = href;
                } else {
                    const json = await api.getUnionid({
                        code: this.$route.query.code,
                    });
                    this.unionid = json.data.unionid;
                    localStorage.setItem('unionid',this.unionid);
                    this.getKechengList(0);
                }
            }
            else{
                this.getKechengList(0);
            }
        },
        data() {
            return {
                unionid: '',
                kechenglist: [],
                scroll: '',
                isend: false,
            }
        },
        methods: {
            async getKechengList(e) {
                if (this.isend) return;
                let json = await api.getKechengList({
                    unionid: this.unionid,
                    scroll: this.scroll,
                })
                if (json.data.length < 10) this.isend = true;
                if (e) this.kechenglist = json.data.concat(this.kechenglist);
                else this.kechenglist = this.kechenglist.concat(json.data);
                this.scroll = json.scroll;
            },
            goTohtml(item) {
                this.$router.push({
                    path: './kecheng',
                    query: {
                        json: item,
                    }
                });
            },
        },
    }
</script>

<style lang="less">
    .kechenglist {
        background: #ffffff;
        .wenzhang {
            background: #ffffff;
            width: 92%;
            margin-left: 4%;
            padding-top: 10px;
            padding-bottom: 10px;
            border-bottom: 1px solid #efefef;
            .kuai {
                display: flex;
                justify-content: space-between;
                .textkuang {
                    width: 80%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    .title {
                        color: #333;
                        text-align: left;
                        font-size: 15px;
                    }
                    .minlan {
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        text-align: center;
                        height: 10px;
                        line-height: 10px;
                        .laiyuan {
                            font-size: 10px;
                        }
                        .yigoumai {
                            width: 40px;
                            color: #fff;
                            background: #ff7e00;
                            border-radius: 5px;
                            font-size: 10px;
                        }
                    }
                }
                .image {
                    margin-left: 16px;
                    height: 70px;
                    width: 124px;
                }
            }
        }
    }
    .textend {
        padding-top: 30px;
        padding-bottom: 30px;
        text-align: center;
    }
</style>



