<template>
  <div id="app">
    <keep-alive>
     <router-view v-if="$route.meta.keepAlive">
      <!-- 这里是会被缓存的视图组件 -->
     </router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive">
     <!-- 这里是不被缓存的视图组件 -->
    </router-view>
  </div>
</template>

<script>
  import Vue from "vue";
  export default {
    name: "app",
  };
</script>

<style>
  body {
    padding: 0px;
    margin: 0px auto;
  }
  a {
    text-decoration: none;
  }
  #app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: left;
    color: #2c3e50;
  }
</style>



