<script>
  import api from "@/api/index";
  import wx from 'weixin-js-sdk';
  export default {
    async mounted() {
      if (!localStorage.getItem('unionid')) {
        if (!this.$route.query.code) {
          var href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx2cc1bc5a412d44d2&redirect_uri=http%3A//www.lianaizhuli.com/huiyuan&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect';
          window.location.href = href;
        } else {
          const json = await api.getUnionid({
            code: this.$route.query.code,
          });
          this.unionid = json.data.unionid;
          localStorage.setItem('unionid',this.unionid);
        }
      }
      else{
        this.unionid=localStorage.getItem('unionid');
      }
    },
    async created() {},
    data() {
      return {
        unionid: '',
        nowtime: 0,
        currentTab: 1,
        paySign_data: {},
        detail: ['', '月会员(1个月)', '年会员(1年)', '私教一个月(送年会员)', '私教3个月(送年会员)', '私教一年(送终身会员)', '联盟会员(含私教终身)'],
      }
    },
    methods: {
      swichtab(e) {
        this.currentTab = e;
      },
      tiaoqizhifu() {
        let that = this;
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId": that.paySign_data.appId, //公众号名称，由商户传入     
            "timeStamp": that.paySign_data.timeStamp, //时间戳，自1970年以来的秒数     
            "nonceStr": that.paySign_data.nonceStr, //随机串     
            "package": that.paySign_data.package,
            "signType": that.paySign_data.signType, //微信签名方式：     
            "paySign": that.paySign_data.paySign //微信签名 
          },
          function(res) {
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              that.$message({
                type: 'success',
                message: '支付成功!请到恋爱联盟小程序开启您的会员体验吧~',
                duration: 10000
              });
            }
            else{
              that.$message({
                type: 'warning',
                message: '您未授权或支付取消',
                duration: 10000
              });
            }
          });
      },
      async get_prepay_id() {
        var timestamp = Date.parse(new Date());
        if (timestamp - this.nowtime < 3000) {
          return false;
        } else {
          this.nowtime = timestamp;
        }
        const json = await api.get_prepay_id({
          'unionid': this.unionid,
          'zhifutype': this.currentTab,
          'detail': this.detail[this.currentTab],
        });
        this.paySign_data = json.data;
        this.tiaoqizhifu();
      },
      weixinzhifu() {
        this.get_prepay_id();
      },
    },
  }
</script>

