import Vue from 'vue';
import Router from 'vue-router';

import huiyuan from '@/pages/huiyuan';
import kechenglist from '@/pages/kechenglist';
import kecheng from '@/pages/kecheng';
import kechengneirong from '@/pages/kechengneirong';

Vue.use(Router);

let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/huiyuan',
      component: huiyuan,
      name: '会员',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/kechenglist',
      component: kechenglist,
      name: '课程列表',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
    {
      path: '/kecheng',
      component: kecheng,
      name: '课程',
      meta: {
        keepAlive:true,
        huancun:false,
      },
    },
    {
      path: '/kechengneirong',
      component: kechengneirong,
      name: '课程内容',
      meta: {
        keepAlive:false,
        huancun:false,
      },
    },
  ],
});
router.beforeEach((to, from,next) => {
  if (from.path=='/kechenglist'&&to.path=='/kecheng') {
    to.meta.huancun=true;
   }
   else{
    to.meta.huancun=false;
   }
   next()
})

export default router;



