<template>
  <div class="kecheng">
    <img class="banner" mode="widthFix" :src="kecheng.image" />
    <div class="title">{{kecheng.title}}</div>
    <div class="tab-h">
      <div v-for="(item,index) in tablist" :key="index" :class="currentTab==index ? 'tab-item active' : 'tab-item'" @click="swichNav(index)">{{item}}</div>
    </div>
    <img class="banner" v-if="currentTab==0" mode="widthFix" :src="kecheng.url" />
    <div v-else class="libiao">
      <div v-for="(item,index) in kecheng.children" :key="index">
        <div class="titlekuang" @click="goTohtml(item,index)">
          <div class="itemtitle">{{item.title}}</div>
          <div class="yigoumai" v-if="kecheng.yigoumai">去学习</div>
          <div v-else>
            <div class="yigoumai" v-if="index<1||(index<2&&kecheng.children.length>=5)">免费</div>
            <img class="suo" v-else src="../assets/suo.png" />
          </div>
        </div>
      </div>
    </div>
    <div class="kuang" v-if="kecheng.yigoumai==0">
      <div class="text" @click="zhifukecheng">特价 {{kecheng.jiage}}</div>
    </div>
  </div>
</template>

<script>
  import api from "@/api/index";
  export default {
    mounted() {
      this.unionid = localStorage.getItem('unionid');
      this.kecheng = this.$route.query.json;
    },
    activated() {
      if (this.$route.meta.huancun) {
        this.kecheng = this.$route.query.json;
        this.currentTab = 0;
      }
    },
    data() {
      return {
        unionid: '',
        kecheng: {},
        currentTab: 0,
        paySign_data: {},
        tablist: ['课程介绍', '课程列表'],
      }
    },
    methods: {
      onShareAppMessage() {
        return {
          title: "恋爱联盟",
          path: "/pages/home"
        };
      },
      tiaoqizhifu() {
        let that = this;
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId": that.paySign_data.appId, //公众号名称，由商户传入     
            "timeStamp": that.paySign_data.timeStamp, //时间戳，自1970年以来的秒数     
            "nonceStr": that.paySign_data.nonceStr, //随机串     
            "package": that.paySign_data.package,
            "signType": that.paySign_data.signType, //微信签名方式：     
            "paySign": that.paySign_data.paySign //微信签名 
          },
          function(res) {
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              that.kecheng.yigoumai = 1;
              that.$message({
                type: 'success',
                message: '支付成功!为更方便阅读请到恋爱联盟小程序开启您的课程学习吧~',
                duration: 10000
              });
            } else {
              that.$message({
                type: 'warning',
                message: '您未授权或支付取消',
                duration: 10000
              });
            }
          });
      },
      async zhifukecheng() {
        var timestamp = Date.parse(new Date());
        if (timestamp - this.nowtime < 3000) {
          return false;
        } else {
          this.nowtime = timestamp;
        }
        const json = await api.get_kechengprepay_id({
          'unionid': this.unionid,
          'kechengid': this.kecheng.id,
          'detail': '课程：' + this.kecheng.title,
        });
        this.paySign_data = json.data;
        this.tiaoqizhifu();
      },
      goTohtml(item, index) {
        if (this.kecheng.yigoumai || index < 1 || (index < 2 && this.kecheng.children.length >= 5)) {
          var kefenxiang = '0'
          if (index < 1 || (index < 2 && this.kecheng.children.length >= 5)) kefenxiang = '1';
          this.$router.push({
            path: './kechengneirong',
            query: {
              id: item.id,
              kechengjson: JSON.stringify(this.kecheng),
              kefenxiang: kefenxiang,
            }
          });
        } else {
          tip.confirm('尊敬的恋爱联盟用户，当前内容为付费阅读，请先购买~');
        }
      },
      swichNav(e) {
        this.currentTab = e;
      },
    },
  }
</script>

<style lang="less">
  .kecheng {
    background: #ffffff;
    color: #000;
    word-break: break-all;
    text-align: center;
    padding-bottom: 40px;
    .banner {
      width: 100%;
    }
    .title {
      color: #333;
      font-size: 20px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
    .tab-h {
      display: flex;
      justify-content: space-around;
      height: 40px;
      line-height: 40px;
      font-size: 20px;
      border-bottom: 2px solid #dfdfdf;
      .tab-item {
        color: #000;
        margin-left: 20px;
        margin-right: 20px;
        display: inline;
      }
      .active {
        background: #ffffff;
        color: #ff7e00;
        margin-left: 20px;
        margin-right: 20px;
        display: inline;
        border-bottom: 2px solid #ff7e00;
      }
    }
    .libiao {
      padding-bottom: 50px;
    }
    .titlekuang {
      width: 90%;
      margin-left: 5%;
      display: flex;
      justify-content: space-between;
      border-bottom: 2px solid #dfdfdf;
      min-height: 50px;
      line-height: 50px;
    }
    .itemtitle {
      text-align: left;
      color: #333;
    }
    .yigoumai {
      margin-top: 19px;
      width: 40px;
      height: 12px;
      line-height: 12px;
      color: #fff;
      background: #ff7e00;
      border-radius: 10px;
      font-size: 12px;
    }
    .suo {
      margin-top: 15px;
      width: 20px;
      height: 20px;
    }
    .kuang {
      position: fixed;
      bottom: 0;
      width: 100%;
      height: 40px;
      .text {
        margin-left: 20%;
        width: 60%;
        border-radius: 10px;
        color: #ffffff;
        text-align: center;
        background: #09BB07;
        line-height: 40px;
      }
    }
  }
</style>



