<template>
    <div class="kechengneirong">
        <div class="title">{{kecheng.title}}</div>
        <div v-html="kecheng.content"></div>
    </div>
</template>

<script>
import api from '@/api/index';
export default {
  created() {
    this.unionid = localStorage.getItem('unionid') || '';
    this.neirongid = this.$route.query.id;
    this.kechengjson = JSON.parse(this.$route.query.kechengjson);
    this.kefenxiang = this.$route.query.kefenxiang;
    this.getKecheng();
  },
  data() {
    return {
      unionid: '',
      neirongid: '',
      kecheng: {},
      kechengjson: {},
      kefenxiang: '0'
    };
  },
  methods: {
    async getKecheng() {
      let json = await api.getKecheng({
        unionid: this.unionid,
        neirongid: this.neirongid,
        kechengid: this.kechengjson.id || '',
        kefenxiang: this.kefenxiang
      });
      if (json.MSG == 'YES') {
        this.kecheng = json.data;
      } else {
        this.$message({
          type: 'warning',
          message: '尊敬的恋爱联盟用户，当前内容为付费阅读，请先购买~',
          duration: 10000
        });
        var href = 'http://www.lianaizhuli.com/kechenglist';
        window.location.href = href;
      }
    }
  }
};
</script>

<style lang="less">
.kechengneirong {
  background: #ffffff;
  padding-left: 10px;
  padding-right: 10px;
  color: #333;
  word-break: break-all;
  text-align: justify;
  padding-bottom: 100px;
}
.title {
  color: #333;
  font-size: 30px;
}
</style>



